<template>
  <div class="wrapper">
    <Navbar @setSearch="searchValue = $event" />
    <NotesList :notes="filterNotes" @change="change" @delNote="delNote" />
    <AddBtn @click="isModalOpen = true, edit = false" />
    <Modal v-show="isModalOpen" @close="isModalOpen = false" @addNote="addNote" :editedNote="editedNote" :edit="edit"
      @changeNote="changeNote" />
  </div>
</template>
<script>
import Navbar from '@/components/Navbar.vue'
import AddBtn from './components/AddBtn.vue'
import NotesList from './components/NotesList.vue'
import Modal from './components/Modal.vue'
export default {
  name: 'App',
  components: { Navbar, NotesList, AddBtn, Modal },
  data() {
    return {
      isModalOpen: false,
      notes: [],
      editedNote: null,
      edit: false,
      searchValue: '',
    }
  },
  methods: {
    addNote(note) {
      this.notes = [...this.notes, note]
    },
    qwe(e) {
      console.log(e);

    },
    delNote(id) {
      this.notes = this.notes.filter((note) => note.id != id)
    },
    change(id) {
      this.isModalOpen = this.edit = true
      let currentNote = this.notes.find((note) => note.id == id)
      this.editedNote = currentNote
    },
    changeNote(newNote) {
      this.notes.forEach((note) => {
        if (note.id == newNote.id) {
          note.title = newNote.title
          note.text = newNote.text
          note.date = newNote.date
        }
      })
    },
    getNotes() {
      let localNotes = localStorage.notes
      if (localNotes) {
        this.notes = JSON.parse(localNotes)
      }
    }
  },
  mounted() {
    this.getNotes()
  },
  computed: {
    filterNotes() {
      return this.searchValue
        ? this.notes.filter((note) => note.title.toLowerCase().includes(this.searchValue.toLowerCase())) : this.notes
    }
  },
  watch: {
    notes: {
      handler() {
        localStorage.notes = JSON.stringify(this.notes)
      },
      deep: true,
    }
  }
}
</script>
