<template>
  <div class="modal" @click="closeModal">
    <div class="modal__block" @click.stop="">
      <h2 class="modal__block-title">{{ !edit ? $t("addNote") : $t("changeNote") }}</h2>
      <div class="modal__block-inputs">
        <label>
          <span>Title</span>
          <input type="text" placeholder="title" v-model="title" />
        </label>
        <label>
          <span>Content</span>
          <input type="text" placeholder="content" v-model="content" />
        </label>
      </div>
      <div class="modal__block-btns">
        <div class="modal__block-btns-btn del" @click="closeModal">{{ $t("cansel") }}</div>
        <div class="modal__block-btns-btn edit" @click="addNote" v-if="!edit">{{ $t("add") }}</div>
        <div class="modal__block-btns-btn edit" @click="changeNote" v-else>{{ $t("change") }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { v4 as uuidv4 } from 'uuid';
export default {
  props: {
    editedNote: Object,
    edit: Boolean
  },
  data() {
    return {
      title: '',
      content: '',
    }
  },
  methods: {
    closeModal() {
      this.$emit('close')
      this.content = this.title = ''
    },
    addNote(e) {
      console.log(e);
      if (this.title != '' && this.content != '') {
        const note = {
          title: this.title,
          date: new Date().toLocaleDateString(),
          text: this.content,
          id: uuidv4(),
        }
        console.log(note.id);
        this.$emit('addNote', note)
        this.closeModal()
      }
    },
    changeNote() {
      if (this.title.length > 2 && this.content.length > 2) {
        const newEditedNote = {
          title: this.title,
          date: new Date().toLocaleDateString(),
          text: this.content,
          id: this.editedNote.id,
        }
        this.$emit('changeNote', newEditedNote)
        this.closeModal()
      }
    }
  },
}
</script>

<style lang="scss" scoped></style>
